.area-name {
  margin: 0.5rem 1rem 0.25rem;
}
.area-description{
  margin: 0 1rem;
}

.area-cafes h4{
  font-size: 1.15rem;
  margin: 1rem;
}

#return{
margin: 0.35rem 1rem;
padding: 0.2rem 0.5rem;
background-color: var(--back-button);
border-color: var(--back-button);
}

#return:hover{
background-color: var(--back-button-hover);
border-color: var(--back-button-hover);
}
#return:focus{
box-shadow: 0 0 0 0.15rem var(--back-button-focus);
}
